import React from "react";
import BaseInputComponent from "./BaseInput.component";
import ButtonIconComponent from "../buttons/ButtonIcon.component";
import IconSearchComponent from "../icons/IconSearch.component";

const SearchInput = ({ onSearch, placeholder }: { placeholder?: string; onSearch: (value: string) => void }) => {
  const [value, setValue] = React.useState("");
  const handleOnSearch = () => {
    onSearch(value);
  };
  return (
    <div className="flex items-center" aria-roledescription="search">
      <div className="flex-1">
        <BaseInputComponent
          placeholder={placeholder || "Search..."}
          type="search"
          onChange={(event) => {
            setValue(event.target.value);
          }}
          className="border-r-0 rounded-e-none"
        />
      </div>
      <ButtonIconComponent onClick={handleOnSearch}>
        <IconSearchComponent className="w-5 h-5" />
      </ButtonIconComponent>
    </div>
  );
};

export default React.memo(SearchInput);
