import React from "react";
import useProductStore from "../../store/hooks/use-product-store.hook";
import ButtonIconComponent from "../../shared-components/buttons/ButtonIcon.component";
import IconEditComponent from "../../shared-components/icons/IconEdit.component";
import IconDeleteComponent from "../../shared-components/icons/IconDelete.component";
import IconDetailComponent from "../../shared-components/icons/IconDetail.component";

const ProductList = ({ keyword }: { keyword?: string }) => {
  const { productList } = useProductStore();
  if (!productList || productList.length === 0) {
    return null;
  }
  return (
    <table className="w-full" aria-roledescription="product list">
      <thead className="border-b-2 font-semibold">
        <tr>
          <th className="text-left p-2">Image</th>
          <th className="text-left p-2">Name</th>
          <th className="text-left p-2">Brand</th>
          <th className="text-left p-2">Description</th>
          <th className="text-left p-2">Price</th>
          <th className="text-left p-2">Discount</th>
          <th className="text-left p-2">Rating</th>
          <th className="text-left p-2">Quantity</th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        {productList?.map((product) => (
          <tr key={product.id} className="border-b">
            <td className="p-2 w-24">
              <img src={product.thumbnail} alt={product.title} className="w-24 rounded" />
            </td>
            <td className="p-2" dangerouslySetInnerHTML={{ __html: highlightKeyword(product.title, keyword) }}></td>
            <td className="p-2">{product.brand}</td>
            <td
              className="p-2 w-56"
              dangerouslySetInnerHTML={{ __html: highlightKeyword(product.description, keyword) }}
            ></td>
            <td className="p-2">{product.price}$</td>
            <td className="p-2">{product.discountPercentage}%</td>
            <td className="p-2">{product.rating}</td>
            <td className="p-2">{product.stock}</td>
            <td className="p-2">
              <div className="flex gap-2 flex-nowrap justify-end items-center">
                <ButtonIconComponent variant="primary">
                  <IconDetailComponent className="w-5 h-5" />
                </ButtonIconComponent>
                <ButtonIconComponent variant="warning">
                  <IconEditComponent className="w-5 h-5" />
                </ButtonIconComponent>
                <ButtonIconComponent variant="danger">
                  <IconDeleteComponent className="w-5 h-5" />
                </ButtonIconComponent>
              </div>
            </td>
          </tr>
        ))}
      </tbody>
    </table>
  );
};

export default React.memo(ProductList);

const highlightKeyword = (text: string, keyword: string) => {
  if (!text || !keyword) return text;
  const regex = new RegExp(keyword, "gi");
  return text.replace(
    regex,
    (match) => `<span class="underline font-semibold text-indigo-500" title='Search Term'>${match}</span>`
  );
};
