import React from "react";
import SectionHeadingComponent from "../shared-components/SectionHeading.component";
import SearchInputComponent from "../shared-components/inputs/SearchInput.component";
import useProductStore from "../store/hooks/use-product-store.hook";
import ProductListComponent from "../products/components/ProductList.component";
import ButtonComponent from "../shared-components/buttons/Button.component";

const ProductPage = () => {
  const [searchKeyword, setSearchKeyword] = React.useState<string>("");
  const { searchProductList, resetProductStore } = useProductStore();
  const handleOnSearch = React.useCallback(
    (value: string) => {
      setSearchKeyword(value);
      if (!value) {
        return;
      }
      searchProductList(value);
    },
    [searchProductList]
  );

  React.useEffect(() => {
    return () => resetProductStore();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div aria-roledescription="product" className="h-full">
      <SectionHeadingComponent title="Product" subtitle="View, edit, add, or delete product" />
      <div className="p-5 h-[calc(100%-64px)]">
        <div className="flex justify-between pb-5">
          <SearchInputComponent placeholder="Search products..." onSearch={handleOnSearch} />
          <div>
            <ButtonComponent text="Add Product" />
          </div>
        </div>
        <div className="h-[calc(100%-56px)] overflow-auto">
          <ProductListComponent keyword={searchKeyword} />
        </div>
      </div>
    </div>
  );
};

export default React.memo(ProductPage);
